@font-face {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  src: url("/source/fonts/ptsans.woff2") format("woff2"), url("/source/fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-style: normal;
  src: url("/source/fonts/ptsansbold.woff2") format("woff2"), url("/source/fonts/ptsansbold.woff") format("woff");
}
a {
  text-decoration: none;
}
.clear-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.clear-paragraph {
  margin: 0;
  padding: 0;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
img {
  width: 100%;
  height: auto;
}
::placeholder {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
.link {
  text-decoration: none;
  color: #ffffff;
  text-align: center;
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.button {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  border: none;
  background-color: #81b3d2;
}
.main-title {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
.main-title--white {
  color: #ffffff;
}
.main-title--bigger {
  font-size: 21px;
}
@media (min-width: 768px) {
  .main-title {
    font-size: 21px;
  }
}
.main-text {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
.main-text--white {
  color: #ffffff;
}
.main-text--black {
  color: #000000;
}
.main-text--bigger {
  line-height: 24px;
}
.main-text--the-biggest {
  line-height: 26px;
}
.page {
  height: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #000000;
  background: #e7e5e5;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  max-width: 1200px;
  margin: auto;
  text-transform: uppercase;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .page__body {
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.page-main {
  background-color: #ffffff;
  text-align: center;
}
.main-header {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 56px;
  background-color: #ffffff;
}
.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -50px;
  z-index: 1;
}
@media (min-width: 1200px) {
  .main-header__logo {
    margin-left: -93px;
  }
}
.main-header__logo-picture {
  display: block;
}
.main-nav {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 56px;
  background-color: #ffffff;
}
.main-nav__wrapper {
  width: 100%;
  height: 100%;
}
.nav-toggle {
  width: 23px;
  height: 22px;
  background: #ffffff url("../img/index/icon-menu.svg") no-repeat 0 0;
  border: none;
  z-index: 1;
}
.main-nav__nav-list {
  text-align: center;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    z-index: 1;
  }
  .main-nav--closed .nav-list__item {
    display: none;
  }
  .main-nav--opened .nav-list__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 56px;
    background-color: #81b3d2;
    padding: 10px;
  }
  .main-nav--opened .nav-list__item:not(:first-child) {
    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
  .main-nav--opened .nav-list__item:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  }
  .main-nav--opened .nav-list::after {
    position: absolute;
    bottom: 0;
    left: 0;
    content: "";
    height: 7px;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    pointer-events: none;
  }
  .nav-list {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
  }
  .main-nav--closed .nav-toggle {
    position: absolute;
    top: 17px;
    right: 0;
    background: #ffffff url("../img/index/icon-menu.svg") no-repeat 0 0;
    z-index: 2;
    margin-left: auto;
    margin-right: 20px;
  }
  .main-nav--opened .nav-toggle {
    position: absolute;
    top: 17px;
    right: 0;
    background: #81b3d2 url("../img/icon-cross.png") no-repeat 0 0;
    z-index: 2;
    margin-left: auto;
    margin-right: 20px;
  }
  .nav-list__nav-link {
    font-weight: 700;
    font-size: 18px;
    line-height: 40px;
  }
}
@media (min-width: 768px) {
  .main-nav__nav-toggle {
    display: none;
  }
  .main-nav__close-button {
    display: none;
  }
  .main-nav__nav-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.nav-list {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
}
.nav-list__nav-link {
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .nav-list__item {
    width: 153px;
  }
  .nav-list__item:nth-child(3n) {
    margin-left: 156px;
  }
  .nav-list__nav-link {
    font-weight: 700;
    font-size: 14px;
    line-height: 26px;
    color: #000000;
  }
}
@media (min-width: 1200px) {
  .nav-list__item {
    width: 239px;
  }
  .nav-list__item:nth-child(3n) {
    margin-left: 244px;
  }
}
.intro-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  color: #333333;
  background-color: #ffffff;
}
.intro-header__main-photo {
  position: relative;
  display: flex;
  align-items: center;
  height: 389px;
  width: 100%;
  min-width: 320px;
  max-height: 518px;
  padding: 20px;
  background: #333333;
  background-image: url("/img/index/index-photo.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .intro-header__main-photo {
    height: 518px;
  }
}
@media (min-resolution: 2dppx) {
  .intro-header__main-photo {
    background-image: url("/img/index/back-desktop@2.jpg");
  }
}
.intro-header__main-photo--smaller {
  height: 158px;
  padding: 45px;
}
@media (min-width: 768px) {
  .intro-header__main-photo--smaller {
    height: 290px;
  }
}
@media (min-width: 768px) {
  .intro-header__main-photo--smaller {
    height: 300px;
  }
}
.intro-header__main-photo::after {
  content: "";
  position: absolute;
  width: 100%;
  min-height: 57px;
  margin-bottom: -1px;
  bottom: 0;
  display: block;
  left: 0;
  background: url("/img/index/intro-triangle.svg") center center no-repeat;
}
.intro-header__main-photo-text {
  margin: auto;
  width: 282px;
}
@media (min-width: 768px) {
  .intro-header__main-photo-text {
    width: 460px;
  }
}
.intro-header__wrapper {
  padding: 12px;
  text-align: center;
}
@media (max-width: 767px) {
  .intro-header__wrapper {
    margin-top: 32px;
  }
  .intro-header__wrapper br {
    display: none;
  }
}
@media (min-width: 768px) {
  .intro-header__wrapper {
    width: 550px;
    margin-top: 64px;
  }
}
@media (min-width: 768px) {
  .intro-header__wrapper {
    margin-top: 114px;
  }
}
.intro-header__header-desc {
  margin-top: 26px;
}
.features__list {
  text-align: center;
  counter-reset: advantages;
  background-color: #eeeeee;
}
.features__item {
  display: flex;
  flex-direction: column;
  background-color: #eeeeee;
  counter-increment: advantages;
}
.features__main-text::before {
  display: block;
  margin: 17px 0;
  content: "— №" counter(advantages) "—";
}
.features__wrapper {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 167px;
  background-color: #eeeeee;
  border-bottom: 1px solid #d6d6d6;
}
.features__wrapper--blue {
  width: 100%;
  min-height: 191px;
  background-color: #81b3d2;
}
.features__wrapper--pic::after {
  display: block;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 260px;
  margin: auto;
  content: "";
  order: 1;
  background-size: cover;
}
.features__wrapper--pic:first-child::after {
  background: #ffffff url("../img/index/photo-city.jpg") center bottom no-repeat;
  background-size: cover;
}
.features__wrapper--pic:nth-child(2)::after {
  background: #ffffff url("../img/index/photo-bridge.jpg") center bottom no-repeat;
  background-size: cover;
  order: -1;
}
.features-item__features-city-section {
  order: 2;
}
@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .features__item {
    min-height: 256px;
  }
  .features__wrapper {
    border: none;
    height: 100%;
    padding: 20px;
  }
  .features__wrapper--pic {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column: span 3;
  }
  .features__wrapper--pic::after {
    grid-column: span 2;
    width: 100%;
    height: 256px;
    background-size: contain;
  }
  .features-city-section__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column: span 3;
  }
  .features-item__features-city-section {
    grid-column: span 3;
  }
}
.features-city-section {
  background-color: #ffffff;
  padding: 35px 20px;
}
.city-feature {
  position: relative;
}
.city-feature::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  background-position: center top;
  background-repeat: no-repeat;
}
.city-feature--accommodation::before {
  background-image: url("/img/index/icon-house.svg");
}
.city-feature--suveniurs::before {
  background-image: url("/img/index/icon-gift.svg");
}
.city-feature--food::before {
  background-image: url("/img/index/icon-burger.svg");
}
@media (max-width: 767px) {
  .features-city-section__city-feature {
    text-align: left;
    margin-left: 52px;
    margin-bottom: 26px;
  }
  .features-city-section__city-feature:last-child {
    margin-bottom: 0;
  }
  .city-feature::before {
    top: -5px;
    left: -52px;
  }
  .features-item-city__description {
    margin-top: 14px;
  }
}
@media (min-width: 768px) {
  .features-city-section {
    min-height: 328px;
    padding: 0;
  }
  .features-city-section__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    padding: 0;
  }
  .features-city-section__city-feature {
    margin-top: 158px;
    padding: 0 26px;
  }
  .city-feature::before {
    width: 75px;
    height: 75px;
    top: -101px;
    left: 50%;
    margin-left: -37px;
    background-size: contain;
  }
  .city-feature--suveniurs {
    order: 1;
  }
  .features-item-city__description {
    margin-top: 21px;
  }
}
.search {
  background-color: #ffffff;
  padding: 17px;
  margin: auto;
  text-align: center;
}
.search__button {
  width: 280px;
  padding: 14px;
}
/* -----hostel-search----- */
.hostel-search {
  position: relative;
  padding-top: 60px;
}
.hostel-search__main-text {
  font-weight: 700;
  font-size: 30px;
  line-height: 24px;
  margin-bottom: 28px;
}
.hostel-search__additional-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: var(--basic-dark-300);
  margin-bottom: 40px;
}
.button {
  font-family: 'PTSans';
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  color: var(--basic-white);
  border: none;
}
@keyframes popup {
  0% {
    transform: scale(1, 0.5) translateY(-395px);
  }
  /* 50%{
       transform: scale(1, 0.9) translateY(-200px);
  } */
  100% {
    transform: scale(1, 1) translateY(0);
  }
}
/* -----main-form----- */
.form-wrapper {
  position: relative;
  background-color: #ffffff;
  z-index: 2;
}
.button-search {
  width: 568px;
  height: 86px;
  background-color: #766357;
}
.button-search:hover {
  background-color: #604E43;
  outline: none;
}
.button-search:active {
  background-color: #503E33;
}
.main-form {
  position: absolute;
  left: 316px;
  top: 280px;
  width: 568px;
  height: 395px;
  padding: 55px;
  background-color: #ffffff;
  box-shadow: 0px 7px 15px rgba(0, 1, 1, 0.15);
  font-weight: 700;
  font-size: 14px;
  line-height: 26px;
  transform-origin: top center;
  z-index: 1;
}
.open {
  animation-name: popup;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}
.hidden {
  animation-name: popup;
  animation-duration: 2s;
  animation-direction: reverse;
  animation-fill-mode: forwards;
}
.input-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 458px;
  margin-bottom: 29px;
}
.main-form .input-wrapper:nth-of-type(3) {
  margin-bottom: 0;
}
.input-wrapper-two {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
}
.input-wrapper .input-wrapper-two:first-child {
  width: 227px;
  margin-right: 53px;
}
.input-wrapper .input-wrapper-two:nth-child(2) {
  width: 180px;
}
.form__input-check-in {
  width: 346px;
  padding: 6px 13px;
  background-color: #F2F2F2;
  border: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 26px;
}
.button-calend {
  position: absolute;
  right: 8px;
  top: 7px;
  width: 21px;
  height: 23px;
  border: none;
  background: url("/img/calendar.svg") no-repeat 0 0;
}
.button-calend:hover,
.button-calend:focus {
  background: url("/img/calendar-hover.png") no-repeat 0 0;
}
.button-calend:active {
  background: url("/img/calendar-active.png") no-repeat 0 0;
}
.form__input-category-people {
  width: 114px;
  padding: 6px 13px;
  background-color: #F2F2F2;
  border: none;
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 26px;
}
.form__input-category-people:hover {
  background-color: #EBEBEB;
}
.form__input-category-people:focus {
