.button-minus {
  position: absolute;
  right: 89px;
  top: 50%;
  width: 12px;
  height: 3px;
  margin-top: -2px;
  border: none;
  background-image: url("/img/Rectangle.svg");
  background-position: 0 0;
  background-size: 12px 3px;
  background-repeat: no-repeat;
}
.button-minus:hover,
.button-minus:focus {
