  background-position: 0 0;
  background-size: 12px 3px;
  background-repeat: no-repeat;
}
.button-plus {
  position: absolute;
  right: 14px;
  top: 50%;
  width: 11px;
  height: 11px;
  margin-top: -6px;
  border: none;
  background-image: url("/img/Union.svg");
  background-position: 0 0;
  background-size: 11px 11px;
  background-repeat: no-repeat;
}
.button-plus:hover,
.button-plus:focus {
  background-image: url("/img/Union-black.svg");
  background-position: 0 0;
  background-size: 11px 11px;
  background-repeat: no-repeat;
  outline: none;
}
.button-plus:active {
  background-image: url("/img/Union-blue.svg");
  background-position: 0 0;
  background-size: 11px 11px;
  background-repeat: no-repeat;
}
.form-button-submit {
  width: 100%;
  height: 58px;
  padding: 16px;
  margin-top: 54px;
  background-color: #81b3d2;
}
.form-button-submit:hover,
.form-button-submit:focus {
  background-color: #669ec0;
}
.form-button-submit:active {
  background-color: #5496bd;
}
.name-section {
  display: flex;
  width: 100%;
  height: 58px;
  margin-bottom: 20px;
  align-items: center;
}
.name-section__input-name {
  margin-left: 10px;
  padding: 0 22px;
  background-color: #f2f2f2;
  border: none;
  height: 100%;
  width: 100%;
}
.contact-section {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 20px;
  align-items: flex-start;
}
.label-contact {
  margin-left: 22px;
}
.contact-section__input-contact {
  display: block;
  margin-top: 5px;
  padding: 0 22px;
  background-color: #f2f2f2;
  border: none;
  height: 58px;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center right 19px, 0 0;
  padding-right: 78px;
}
.input-contact--tel {
  background-image: url("../img/form/phone.svg"), linear-gradient(to left, #e6e6e6 58px, transparent 58px);
}
.input-contact--email {
  background-image: url("../img/form/mail.svg"), linear-gradient(to left, #e6e6e6 58px, transparent 58px);
}
.review-score-section {
  display: flex;
  align-items: center;
  min-height: 50px;
}
.review-score-section__label-review-score {
  position: relative;
  display: flex;
  align-items: center;
  height: 50px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
  padding-left: 78px;
}
.review-score-section__label-review-score::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 50px;
  height: 50px;
  border: 5px #f2f2f2;
  border-radius: 50%;
  background-color: #f2f2f2;
}
.review-score-section__input-score:checked ~ .review-score-section__label-review-score::after {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -7px;
  margin-left: 20px;
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #000000;
}
.visited-place-section {
  display: flex;
  align-items: center;
  min-height: 50px;
}
.visited-place-section__label-place {
  position: relative;
  display: flex;
  align-items: center;
  height: 50px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
  padding-left: 78px;
}
.visited-place-section__label-place::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 50px;
  height: 50px;
  border: 5px #f2f2f2;
  border-radius: 5px;
  background-color: #f2f2f2;
}
.visited-place-section__input-place:checked ~ .visited-place-section__label-place::after {
  position: absolute;
  top: 15px;
  left: 16px;
  content: "";
  width: 20px;
  height: 12px;
  border: 3px solid #000000;
  border-top: transparent;
  border-right: transparent;
  transform: rotate(-45deg);
  background-color: transparent;
}
.text-review-section {
  display: flex;
  align-items: center;
  min-height: 50px;
}
.text-review-section textarea {
  width: 100%;
  max-height: 160px;
  background-color: #f2f2f2;
  border: none;
  padding: 13px 21px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.map iframe {
  width: 100%;
  height: 350px;
  border: none;
}
figure {
  margin: 0;
  padding: 0;
}
.page-footer {
  width: 100%;
  min-height: 233px;
  background-color: #ffffff;
}
.page-footer__wrapper {
  width: 280px;
